import * as actionTypes from './actionTypes';
import axios from 'axios';

const jobApplySuccess = () => {
  return {
    type: actionTypes.JOB_APPLY_SUCCESS
  };
};

const jobApplyFinished = () => {
  return {
    type: actionTypes.JOB_APPLY_FINISHED
  }
}

export const jobApply = (info) => {
  return dispatch => {
    axios.post("https://pizza-land-babe6-default-rtdb.firebaseio.com/jobApply.json", info)
      .then(response => {

        dispatch(jobApplySuccess());
        setTimeout(() => {
          dispatch(jobApplyFinished());
        }, 2000);
      })
      .catch(error => {
        console.log(error);
      })
  }
}