import * as actionTypes from '../actions/actionTypes';

const initialState = {
  jobApplicationInfo: null,
  applicationFinished: false
};

const reducer = (state = initialState, action) => {
  switch(action.type) {
    case actionTypes.JOB_APPLY_SUCCESS:
      return {
        ...state,
        applicationFinished: true
      };
    case actionTypes.JOB_APPLY_FINISHED:
      return {
        ...state,
        applicationFinished: false
      };
    default:
      return state;
  }
};

export default reducer;